	module.exports = {"data":{"site":{"siteMetadata":{"title":"Kostas Bariotis' Blog","description":"I'm Kostas Bariotis, a web developer, a proud wanderer and a passionate doer. My mission is to write clean and efficient code, to solve problems on the web and to learn something more.","siteUrl":"https://kostasbariotis.com"}}},"pathContext":{"posts":[{"excerpt":"I watch a lot of people on the Internet.  Authors ,  film makers ,  music producers . I often wonder where do these guys get inspiration and motivation. It came down to be one thing. Inspiration is like metabolism. You have to put it in work in order...","html":"<p>I watch a lot of people on the Internet. <a href=\"http://sethgodin.typepad.com\">Authors</a>, <a href=\"https://www.youtube.com/user/caseyneistat\">film makers</a>, <a href=\"https://www.youtube.com/user/Hopsintv\">music producers</a>. I often wonder where do these guys get inspiration and motivation.</p>\n<p>It came down to be one thing.</p>\n<p>Inspiration is like metabolism. You have to put it in work in order to start working. It won't work by it's own. If you just sit and wait, it won't start working.</p>\n<p>Do. Be a doer. Regularly.</p>\n<blockquote>\n<h3><em>\"The first rule of doing work that matters: Going to work on a regular basis.\"</em><br/><br/></h3>\n<p>Seth Godin</p>\n</blockquote>","id":"/Users/Bariotis/Dev/develop/kostasbariotis.com/src/pages/inspiration-motivation-action/index.md absPath of file >>> MarkdownRemark","timeToRead":1,"frontmatter":{"date":"2015-09-30T07:03:28.000Z","path":"/inspiration-motivation-action/","tags":"inspiration, motivation","title":"Action, Inspiration, Motivation","draft":null}}],"tag":"motivation","pagesSum":1,"page":1}}

